import React, { useState } from 'react';
import { useMutation, useQuery } from '@apollo/react-hooks';
import {
  CREATE_MYDIARY,
  GET_QUESTIONSOFME,
  GET_ALLDIARYQUESTION,
} from 'Routes/Diary/DiaryQuries';
import { READ_MYDIARY } from 'Routes/Me/MeQueries';
import Survey from 'Components/Survey';
import { useParams } from 'react-router-dom';
import Loader from 'Components/Loader';

export default () => {
  const [diary, setDiary] = useState({
    good: undefined,
    content: '',
  });
  const [createMyDiary] = useMutation(CREATE_MYDIARY, {
    refetchQueries: () => [{ query: READ_MYDIARY }],
  });

  const { data: allQuestionData, loading: allQuestionLoading } = useQuery(
    GET_ALLDIARYQUESTION
  );
  const param = useParams();
  const questionId = param.questionId;
  console.log('questionId : ', questionId);
  //const questionId = 'ck5g827hw00i00740yi5sl2d9';
  if (allQuestionLoading) {
    return <Loader />;
  } else {
    const singleQuestion = allQuestionData.getAllQuestion.find(
      question => question.id === questionId
    );
    console.log('singleQuestion : ', singleQuestion);
    return (
      <Survey
        diary={diary}
        setDiary={setDiary}
        createMyDiary={createMyDiary}
        singleQuestion={singleQuestion}
        isPushQuestion={true}
      />
    );
  }
};
