import React from 'react';
import styled from 'styled-components';
import BlackOutLineButton from './BlackOutLineButton';
import mainImage from '../assets/image/1.jpg';
const Wrapper = styled.div`
  display: grid;
  grid-template-rows: 8vh 40vh 20vh 25vh;
  align-items: center;
  background: white;
  justify-items: center;
`;

const TextWrapper = styled.div`
  display: grid;
  text-align: center;
`;

const OutLineButtonWrapper = styled.div`
  display: grid;
  align-items: center;
  justify-items: center;
  background: white;
  width: 100%;
  height: 100%;
`;

const Title = styled.div`
  font-family: 'KakaoB';
  font-size: 25px;
`;

const MainImageWraper = styled.div`
  height: 100%;
  width: 100vw;
  display: grid;
  justify-items: center;
  align-items: center;
  max-width: 500px;
`;

const MainImage = styled.img`
  background: #dee2e6;
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
  height: auto;
`;

export default ({ setAction }) => {
  console.log('SurveyDescription');
  return (
    <Wrapper>
      <Title> </Title>
      <MainImageWraper>
        <MainImage src={mainImage} />
      </MainImageWraper>
      <TextWrapper>
        {/* prettier-ignore */}
        <pre>
{`
     
  .

   
  ,

     .
`}
      </pre>
      </TextWrapper>
      <OutLineButtonWrapper>
        <BlackOutLineButton
          style={{ width: '10vw' }}
          text=""
          onClick={() => setAction('SelectQuestion')}
        />
      </OutLineButtonWrapper>
    </Wrapper>
  );
};

