import React, { useState, useMemo } from 'react';
import styled from 'styled-components';
import mainImage0 from '../assets/image/19.jpg';
import mainImage1 from '../assets/image/2.jpg';
import mainImage2 from '../assets/image/24.jpg';
import mainImage3 from '../assets/image/12.jpg';
import mainImage4 from '../assets/image/6.jpg';
import { useHistory } from 'react-router-dom';
import { WhiteBackArrow } from './Icons';

const Wrapper = styled.div`
  display: grid;
  grid-template-rows: 6vh 32vh;
  align-items: start;
  background: white;
  justify-items: center;
`;

const TextWrapper = styled.div`
  display: grid;
  align-content: center;
  font-size: 0.8rem;
  font-family: 'KakaoL';
  font-weight: 600;
  padding-left: 4vw;
  padding-bottom: 4vw;
  line-height: 1.4rem;
`;

const Title = styled.div`
  display: grid;
  justify-items: start;
  align-items: center;
  font-family: 'KakaoB';
  width: 100%;
  height: 100%;
  margin-bottom: 1vh;
`;

const TitleText = styled.div`
  display: grid;
  align-self: center;
  font-size: 1.5rem;
`;

const MainImageWraper = styled.div`
  position: relative;
  height: 32vh;
  width: 100%;
  display: grid;
  justify-items: center;
  align-items: center;

  max-width: 500px;
`;

const MainImage = styled.img`
  position: absolute;
  background: #dee2e6;
  align-self: start;
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
  height: 32vh;
`;

const ThemeHandlerWrapper = styled.div`
  display: grid;
  width: 100%;
  height: 6vh;
  grid-template-columns: 3fr 4fr 3fr;
  justify-items: center;
  align-items: center;
  background-color: ${props => props.theme.activeColor};
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.05);
  position: fixed;
`;
const BackArrowWrapper = styled.div`
  display: grid;
  grid-template-columns: 1fr 2fr;
  justify-self: center;
  align-self: center;
`;
const ArrowWrapper = styled.div`
  display: grid;
  grid-template-columns: 2fr 1fr;
  justify-self: center;
  align-self: center;
`;
const SvgWrapper = styled.div`
  display: grid;
  transform: rotate(180deg);
`;
const TabTitleText = styled.div`
  display: grid;
  align-self: center;
  justify-self: center;
  font-size: 1.5rem;
  color: white;
`;
const TabTitle = styled.div`
  display: grid;
  justify-items: center;
  align-items: center;
  font-family: 'KakaoB';
  color: white;
  width: 100%;
  height: 100%;
`;
const Text = styled.div`
  display: grid;
  align-self: center;
  justify-self: center;
  color: white;
`;

export default () => {
  const [count, setCount] = useState(0);
  const history = useHistory();
  console.log('SurveyDescription');
  const mainImage = useMemo(() => {
    switch (count) {
      case 0:
        return mainImage0;
      case 1:
        return mainImage1;
      case 2:
        return mainImage2;
      case 3:
        return mainImage3;
      case 4:
        return mainImage4;
      default:
        return mainImage0;
    }
  }, [count]);

  return (
    <Wrapper>
      <div></div>
      <ThemeHandlerWrapper>
        <BackArrowWrapper
          onClick={() =>
            setCount(count === 0 ? history.push('/diary') : count - 1)
          }>
          <WhiteBackArrow />
          <Text></Text>
        </BackArrowWrapper>
        <TabTitle>
          <TabTitleText></TabTitleText>
        </TabTitle>
        <ArrowWrapper
          onClick={() =>
            setCount(count === 4 ? history.push('/diary') : count + 1)
          }>
          <Text>{count === 4 ? '' : ''}</Text>
          <SvgWrapper>
            <WhiteBackArrow />
          </SvgWrapper>
        </ArrowWrapper>
      </ThemeHandlerWrapper>
      <MainImageWraper>
        <MainImage src={mainImage} />
      </MainImageWraper>
      <Title>
        <TitleText>{contents[count].title}</TitleText>
      </Title>
      <TextWrapper>
        {/* prettier-ignore */}
        <pre>
        {contents[count].content}
      </pre>
      </TextWrapper>
    </Wrapper>
  );
};

