import React, { useState } from 'react';
import { FEED_QUERY, UPLOAD } from './WriteQuries';
import useInput from '../../Hooks/useInput';
import { toast } from 'react-toastify';
import axios from 'axios';
import { useMutation, useQuery } from '@apollo/react-hooks';
import WritePresenter from './WritePresenter';
import { READ_MYDATA } from '../../sharedQueries';
import Loader from 'Components/Loader';

export default () => {
  const [fileUrl, setFileUrl] = useState('');
  const think = useInput('');
  const [postUploadMutation] = useMutation(UPLOAD, {
    refetchQueries: () => [{ query: FEED_QUERY }],
  });
  const { data, loading } = useQuery(READ_MYDATA);

  const upload = async event => {
    console.log('upload');
    const file = event.target.files[0];
    console.log('file : ', file);
    const formData = new FormData();
    formData.append('file', file);
    try {
      const {
        data: { path },
      } = await axios.post('https://boongyee.com/api/upload', formData, {
        headers: {
          'content-type': 'multipart/form-data',
        },
      });
      if (path === undefined) {
        console.log('path:undefined');
        setFileUrl('upLoadFail');
      } else {
        console.log('path');
        setFileUrl(path);
        toast.success(
          ` ! " ?"  
        .`,
          { autoClose: 5000 }
        );
      }
    } catch (e) {
      console.log('upload fail : ', e);
      setFileUrl('upLoadFail');
    }
  };

  const onWriteSubmit = async event => {
    event.preventDefault();
    console.log('');
    console.log(fileUrl === '');
    if (fileUrl === '' || fileUrl === 'upLoadFail') {
      toast.info('    .');
    } else {
    }

    console.log(fileUrl);
    await postUploadMutation({
      variables: {
        title: '',
        caption: think.value,
        files: [fileUrl],
      },
    });
  };

  return (
    <>
      {loading ? (
        <Loader />
      ) : (
        <WritePresenter
          myData={data.readMyData}
          upload={upload}
          onWriteSubmit={onWriteSubmit}
          think={think}
        />
      )}
    </>
  );
};
