import React from 'react';
import PropTypes from 'prop-types';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
  useHistory,
} from 'react-router-dom';
import Auth from '../Routes/Auth';
import Feed from '../Routes/Feed';
import Alarm from '../Routes/Alarm';
import Story from '../Routes/Story';
import Etc from '../Routes/Etc';
import Me from '../Routes/Me';
import Search from '../Routes/Search';
import Diary from '../Routes/Diary';
import Theme from '../Routes/Theme';
import App from './App';
import ModelDescription from './ModelDescription';
import SingleQuestion from '../Routes/SingleQuestion';

const LoggedInRoutes = () => {
  console.log('loggedin');
  let history = useHistory();
  console.log(history);
  return (
    <Switch>
      <Route exact path={'/disorder/'} component={Feed} />
      <Route path={'/search'} component={Search} />
      <Route path={'/diary'} component={Diary} />
      <Route path={'/alarm'} component={Alarm} />
      <Route path={'/story'} component={Story} />
      <Route path={'/me'} component={Me} />
      <Route path={'/etc'} component={Etc} />
      <Route path={'/theme'} component={Theme} />
      <Route path={'/app'} component={App} />
      <Route path={'/description'} component={ModelDescription} />
      <Route path={'/:questionId'} component={SingleQuestion} />
    </Switch>
  );
};

const LoggedOutRoutes = () => {
  console.log('loggedOut');
  return (
    <Router>
      <Route path={'/disorder/'} component={Auth} />
      <Route path={'/theme'} component={Theme} />
      <Route path={'/description'} component={ModelDescription} />
      <Redirect from={'*'} to={'/disorder/'} />
    </Router>
  );
};

const AppRouter = ({ isLoggedIn }) => {
  console.log('router');
  return (
    <Router>{isLoggedIn ? <LoggedInRoutes /> : <LoggedOutRoutes />}</Router>
  );
};

AppRouter.propTypes = {
  isLoggedIn: PropTypes.bool.isRequired,
};

export default AppRouter;
