import React from 'react';
import styled from 'styled-components';
import {
  FeedTeam,
  DiaryRead,
  AlarmSound,
  MeUser,
  StoryMessage,
  ETC,
} from '../Components/Icons';
import { NavLink } from 'react-router-dom';

const Wrapper = styled.div`
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  background: ${props => props.theme.whiteColor};
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.05);
`;

const Tab = styled(NavLink)`
  z-index: 10;
  display: grid;
  > i {
    display: grid;
  }

  i > svg {
    width: 50%;
    height: 50%;
    align-self: center;
    justify-self: center;
    color: ${props => props.theme.dimgreyColor};
  }

  &.active {
    & {
      border-bottom: 0.5vh solid ${props => props.theme.activeColor};
    }
    > i > svg {
      color: ${props => props.theme.activeColor};
    }
  }
`;
const SvgWrapper = styled.i``;
export default React.memo(() => {
  return (
    <>
      <Wrapper>
        <Tab exact to={'/disorder/'}>
          <SvgWrapper>
            <FeedTeam />
          </SvgWrapper>
        </Tab>
        <Tab exact to={'/diary'}>
          <SvgWrapper>
            <DiaryRead />
          </SvgWrapper>
        </Tab>
        <Tab exact to={'/alarm'}>
          <SvgWrapper>
            <AlarmSound />
          </SvgWrapper>
        </Tab>
        <Tab exact to={'/story'}>
          <SvgWrapper>
            <StoryMessage />
          </SvgWrapper>
        </Tab>
        <Tab exact to={'/me'}>
          <SvgWrapper>
            <MeUser />
          </SvgWrapper>
        </Tab>
        <Tab exact to={'/etc'}>
          <SvgWrapper>
            <ETC />
          </SvgWrapper>
        </Tab>
      </Wrapper>
    </>
  );
});
