import React from 'react';
import styled from 'styled-components';
import Tab from '../../Components/Tab';
import Life from 'Components/Life';
import Switch from 'react-switch';
import { Success } from '../../Components/Icons';
import Input from 'Components/Input';
import BlackOutLineButton from 'Components/BlackOutLineButton';
import { useHistory } from 'react-router-dom';
const Wrapper = styled.div`
  display: grid;
  grid-template-rows: 7vh 7vh 7vh 7vh 7vh 7vh 7vh 7vh 7vh;
  height: 100vh;
`;

const Alarm = styled.div`
  display: grid;
  grid-template-columns: 6fr 4fr;
  background: white;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.05);
  font-size: 1rem;
  align-items: center;
  width: 100%;
  height: 100%;
`;
const AlarmText = styled.div`
  display: grid;

  align-self: center;
  justify-self: center;
`;
const ContinueButtonWapper = styled.div`
  justify-self: center;
`;
const Empty = styled.div``;
const SwitchWrapper = styled.div`
  display: grid;
  align-self: center;
`;
const Title = styled.div`
  display: grid;
  grid-template-columns: 2fr 4fr 2fr;
  background: ${props => props.theme.activeColor};
  width: 100%;
  height: 100%;
`;
const TitleText = styled.div`
  font-family: 'KakaoB';
  font-size: 1.5rem;
  display: grid;
  align-self: center;
  justify-self: center;
  color: white;
`;
const PushInputWrapper = styled.div`
  display: grid;
  align-items: center;
  justify-items: center;
  height: 7vh;
  width: 100%;
  background: white;
`;

export default ({
  sendPushMutation,
  isSubscribed,
  updateSubscribe,
  myQuestionData,
}) => {
  let history = useHistory();
  return (
    <>
      <Wrapper>
        <Tab />
        <Title>
          <Empty />
          <TitleText></TitleText>
          <SwitchWrapper>
            <Switch
              onChange={updateSubscribe}
              checked={isSubscribed}
              onColor="#07C50E"
              uncheckedIcon={false}
              checkedIcon={false}
              boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
              activeBoxShadow="0px 0px 1px 10px rgba(0, 0, 0, 0.2)"
              // eslint-disable-next-line
              checkedIcon={<Success />}
            />
          </SwitchWrapper>
        </Title>
        {myQuestionData.getQuestionsOfMe.map(question => (
          <Alarm key={question.id}>
            <AlarmText>{question.type}</AlarmText>
            <ContinueButtonWapper>
              <BlackOutLineButton
                width={'30vw'}
                onClick={() => history.push(`/singlequestion${question.id}`)}
                text={' '}
              />
            </ContinueButtonWapper>
          </Alarm>
        ))}

        <PushInputWrapper>
          <BlackOutLineButton
            width={'80vw'}
            text="     ?"
            onClick={async () => await sendPushMutation()}
          />
        </PushInputWrapper>
        <PushInputWrapper>
          <Life />
        </PushInputWrapper>
      </Wrapper>
    </>
  );
};
